  margin-right: 15px;
}

.nav-bar__links a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
}

.nav-bar__links a:hover {
  color: #06810f;
  transition: 0.3s linear;
}

.nav-bar__links .on {
  color: #06810f;
  border-bottom: 2px solid #06810f;
}

